import React from "react";
import { AppHeader, AppNavigator } from "./components";
import {
    AppContext,
    setUserNavLinks,
    setShowFullMenu,
    setUserInfo,
    setFormLinks,
    setActivePage,
} from "./context";
import { fetchInitialData } from "./remote";

import "./remote";
import "./App.less";

const resource = fetchInitialData();

function App() {
    const { state, dispatch } = React.useContext(AppContext);
    const dataProps = resource.payslip.read();

    React.useEffect(() => {
        if (dataProps.navLinks && dataProps.user) {
            dispatch(setUserInfo(dataProps.user));
            dispatch(setUserNavLinks(dataProps.navLinks));
            dispatch(setFormLinks(dataProps.formLinks));
            dispatch(setActivePage(dataProps.activePage));
            // window.Affinity.DBWDomData = {
            //     user: dataProps.user,
            //     navLinks: dataProps.navLinks
            // }
            // document.dispatchEvent(window.Affinity.DBWDomReady)
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <>
            <AppHeader
                title=""
                onMenuClick={() =>
                    dispatch(setShowFullMenu(!state.showFullMenu))
                }
            />
            <AppNavigator
                showFullMenu={state.showFullMenu}
                onCollapseBtnClick={() =>
                    dispatch(setShowFullMenu(!state.showFullMenu))
                }
            />
        </>
    );
}

export default App;
